@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Noto+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
body{margin:0;font-family:'Noto Sans JP', 'Noto Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:11pt;color:#616161}body h1{color:#3958a7}body h2{color:#3958a7}code{font-family:'Source Code Pro', Menlo, Monaco, Consolas, 'Courier New', monospace;font-size:12pt}

.card{margin-top:3rem;margin-bottom:3rem;padding:0rem 0rem 3rem 0rem;box-shadow:10px 10px 14px -9px rgba(200,200,200,0.8);border-radius:12px}.card>*{padding:1rem 3rem}.container{width:90vw;border:solid 1px rgba(230,230,230,0.548)}body{overflow-x:hidden}html[data-theme="dark"] body{background:#222}html[data-theme="dark"] body p{color:#ccc}html[data-theme="dark"] body h1{color:#7ed6ff}html[data-theme="dark"] body h2{color:#222}html[data-theme="dark"] body h3{color:#00e498}html[data-theme="dark"] body img{border:none;box-shadow:none !important;-webkit-filter:invert(90%) hue-rotate(120deg);filter:invert(90%) hue-rotate(120deg)}html[data-theme="dark"] body li{color:#a5a5a5}html[data-theme="dark"] body>*{color:#ccc}html[data-theme="dark"] .section-header{background:#00e498;border-radius:12px 12px 0 0;box-shadow:1px 1px 12px 1px rgba(0,0,0,0.356)}html[data-theme="dark"] .section-header .h2{color:#222}html[data-theme="dark"] .card{background:#222;box-shadow:1px 1px 12px 1px rgba(0,0,0,0.356);border:none}

.navbar{padding-top:1rem;padding-bottom:1rem;background:#162447}.navbar .row .col{display:flex;justify-content:center;align-items:center}.navbar .branding{letter-spacing:1rem;color:#e43f5a;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;font-weight:bold;vertical-align:middle;display:table;-webkit-text-orientation:upright;text-orientation:upright;padding-top:1rem}.navbar .branding:hover{cursor:pointer}.navbar a{color:#ebebeb;border:solid 1px transparent;margin-left:1rem;margin-right:1rem;width:100px;text-align:center}.navbar a:hover{color:#e43f5a;border:solid 1px #e43f5a;transition:border-left 0.15s, border-top 0.15s 0.15s, border-right 0.15s 0.3s, border-bottom 0.15s 0.45s}.navbar .theme-switch-space{display:flex;flex-direction:column;justify-content:center;align-items:center}

.theme-switch-wrapper .MuiSwitch-thumb{background-color:#3958a7}.theme-switch-wrapper .Mui-checked .MuiSwitch-thumb{background-color:#f50057}

.author-pic-wrapper{display:flex;justify-content:center;align-items:center}.author-pic-wrapper .author-pic{height:150px;width:150px;background-size:cover;border-radius:50%;box-shadow:-10px 10px 14px -9px #757575}.gh-stats-wrapper{display:block}.gh-stats-wrapper img{width:50%;height:auto}html[data-theme="dark"] .author-pic-wrapper .author-pic{box-shadow:1px 1px 12px 1px rgba(0,0,0,0.356)}@media (max-width: 600px){.gh-stats-wrapper img{width:100%;height:auto}}

.projects .row{border-bottom:solid 1px #b1b1b1;align-items:center;padding-bottom:1rem}.projects .row .col{padding:1rem 1rem}@media (max-width: 600px){.container{max-width:100%;width:100vw;margin:2rem 0;border-radius:0}.card{width:100vw;margin:2rem 0}}

.quickies{display:flex;justify-content:center;align-items:center;text-align:center;color:#3fe4bb}.quickies .material-icons{color:#162447}html[data-theme="dark"] .quickies{color:#fcfcfc}html[data-theme="dark"] .quickies .material-icons{color:#ff3a5b}

.contact{width:100%;padding:0;display:grid;grid-template-rows:1fr;grid-template-columns:minmax(50px, 1fr) minmax(300px, 5fr);grid-template-areas:"ct-head ct-content"}.contact .contact-head{grid-area:ct-head;padding:1rem;display:flex;justify-content:center;align-items:center;text-align:center;background:#162447;border-right:solid 0.5px #ffffff;border-radius:12px 0 0 12px}.contact .contact-head h5{color:#ccc;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;-webkit-text-orientation:upright;text-orientation:upright;text-transform:uppercase}.contact .contact-content{grid-area:ct-content;padding:1rem;display:flex;flex-direction:column;justify-content:center}.contact .contact-content p>*{word-wrap:break-word !important}@media (max-width: 375px){.contact .contact-head{border-radius:0}}html[data-theme="dark"] .contact{box-shadow:none;background:transparent}html[data-theme="dark"] .contact .contact-head{box-shadow:1px 1px 12px 1px rgba(0,0,0,0.356);margin-right:0.5rem;border-right:solid 1px transparent;border-radius:12px}html[data-theme="dark"] .contact .contact-content{margin-left:0.5rem;box-shadow:1px 1px 12px 1px rgba(0,0,0,0.356);border-radius:12px}@media (max-width: 375px){html[data-theme="dark"] .contact .contact-head{border-radius:0 12px 12px 0}html[data-theme="dark"] .contact .contact-content{border-radius:12px 0px 0px 12px}}


.footer-wrapper{padding:4rem 10%;background:#274285;color:#ebebeb;align-items:center;border:none}.footer-wrapper div{margin:1rem 0}.footer-copyright{border:none;padding:2rem 10%;background:#e43f5a;color:#ebebeb;align-items:center}.footer-copyright div{margin:1rem 0}

.splash-wrapper{display:flex;text-align:center;align-items:center;flex-direction:column;margin:0;padding:0;width:100vw;height:100vh;background:#0c1b3f;color:#e43f5a}.splash-wrapper .splash{display:block;font-size:20pt;margin:2rem 0}.splash-wrapper .splash:hover{cursor:default}.splash-wrapper .scroll-down{position:absolute;bottom:5%;color:#00e498}.splash-wrapper .scroll-down h1{font-size:16pt}

